@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;1,100;1,200;1,300;1,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  border: 0;
  outline: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-tap-highlight-color: transparent;
}

html {
  scroll-behavior: smooth;
}

html, body{
  height: 100%;
}

body{
  background-color: rgb(32, 37, 40);
  background-color: rgb(245, 245, 245);
}

.dark{
  color: rgb(245, 245, 245);
  /* background-color: rgb(25, 25, 25); */
  background-color: #00090e;
}

.center{
  max-width: 1280px;
  width: 100%;
  padding: 0 2%;
}

.Phrase.dark{
  background-color: #051a28 !important;
}

.line.dark{
  /* border-color: white !important; */
  background-color: white !important;
}
button.change{
    cursor: pointer;
    position: fixed;
    left: calc(100% - 38px);
    top: 50%;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
    background-color: transparent;
}

button.dark .boxChange{
    box-shadow: 0px 10px 10px 1px rgb(0, 0, 0) !important;
}


button.change .boxChange{
    width: 30px;
    height: 80px;
    background-color: white;
    border: 1px solid rgb(200, 200, 200);
    box-shadow: 0px 5px 10px 1px rgba(80, 80, 80, 0.329);
    border-radius: 15px;
    position: absolute;
    padding: 2px 0;
}


button.change .boxChange .boll{
    position: relative;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);

    background-color: rgb(235, 235, 235);
    border: 1px solid rgb(200, 200, 200);
    box-shadow: 0px 2px 4px 1px rgba(15, 58, 58, 0.6);

    width: 30px;
    height: 30px;

    border-radius: 50%;
    line-height: 30px;

    color: white;

    padding: 4px;
}

button.change .boxChange .boll .icon{
    -webkit-transform: rotateZ(0);
            transform: rotateZ(0);
    font-size: 20px;
    width: 100%;
    height: 100%;
    line-height: 25px;
    background-image: url(/static/media/sun.b288121d.svg);
    padding: 2px;
}


/* animations */


.animate{
    -webkit-animation: change 1s;
            animation: change 1s;
    -webkit-animation-iteration-count: unset;
            animation-iteration-count: unset;
    -webkit-animation-direction:normal;
            animation-direction:normal;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;  
}

.animateReverse{
    -webkit-animation: changeReverse 1s;
            animation: changeReverse 1s;
    -webkit-animation-iteration-count: unset;
            animation-iteration-count: unset;
    -webkit-animation-direction:normal;
            animation-direction:normal;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;  
}

.iconAnimate{
    -webkit-animation: iconAnimate 1s;
            animation: iconAnimate 1s;
    -webkit-animation-iteration-count: unset;
            animation-iteration-count: unset;
    -webkit-animation-direction:normal;
            animation-direction:normal;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;  
}

.iconReverse{
    -webkit-animation: iconReverse 1s;
            animation: iconReverse 1s;
    -webkit-animation-iteration-count: unset;
            animation-iteration-count: unset;
    -webkit-animation-direction:normal;
            animation-direction:normal;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;  
}


@-webkit-keyframes change {
    from {
        top: 0%;
        box-shadow: 0px 2px 4px 1px rgba(15, 58, 58, 0.6);
    }

    to{
        top: 100%;
        box-shadow: 0px -2px 4px 1px rgba(15, 58, 58, 0.6);
    }
}


@keyframes change {
    from {
        top: 0%;
        box-shadow: 0px 2px 4px 1px rgba(15, 58, 58, 0.6);
    }

    to{
        top: 100%;
        box-shadow: 0px -2px 4px 1px rgba(15, 58, 58, 0.6);
    }
}

@-webkit-keyframes changeReverse {
    from {
        top: 100%;
        box-shadow: 0px -2px 4px 1px rgba(15, 58, 58, 0.6);
    }

    to {
        top: 0%;
        box-shadow: 0px 2px 4px 1px rgba(15, 58, 58, 0.6);
    }

    
}

@keyframes changeReverse {
    from {
        top: 100%;
        box-shadow: 0px -2px 4px 1px rgba(15, 58, 58, 0.6);
    }

    to {
        top: 0%;
        box-shadow: 0px 2px 4px 1px rgba(15, 58, 58, 0.6);
    }

    
}

@-webkit-keyframes iconAnimate {
    from {
        background-image: url(/static/media/sun.b288121d.svg);
        -webkit-transform: rotateZ(0deg);
                transform: rotateZ(0deg);
    }

    50%{
        background-image: url(/static/media/moon.a3325cc3.svg);
    }

    to{
        background-image: url(/static/media/moon.a3325cc3.svg);
        -webkit-transform: rotateZ(360deg);
                transform: rotateZ(360deg);
    }
}

@keyframes iconAnimate {
    from {
        background-image: url(/static/media/sun.b288121d.svg);
        -webkit-transform: rotateZ(0deg);
                transform: rotateZ(0deg);
    }

    50%{
        background-image: url(/static/media/moon.a3325cc3.svg);
    }

    to{
        background-image: url(/static/media/moon.a3325cc3.svg);
        -webkit-transform: rotateZ(360deg);
                transform: rotateZ(360deg);
    }
}

@-webkit-keyframes iconReverse {
    from {
        background-image: url(/static/media/moon.a3325cc3.svg);
        -webkit-transform: rotateZ(360deg);
                transform: rotateZ(360deg);
    }

    to {
        background-image: url(/static/media/sun.b288121d.svg);
        -webkit-transform: rotateZ(0deg);
                transform: rotateZ(0deg);
    }

    
}

@keyframes iconReverse {
    from {
        background-image: url(/static/media/moon.a3325cc3.svg);
        -webkit-transform: rotateZ(360deg);
                transform: rotateZ(360deg);
    }

    to {
        background-image: url(/static/media/sun.b288121d.svg);
        -webkit-transform: rotateZ(0deg);
                transform: rotateZ(0deg);
    }

    
}

